@import url('https://fonts.googleapis.com/css2?family=Square+Peg&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Barlow:wght@400;700&display=swap');

*, 
::before, 
::after{
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

::-moz-selection { /* Code for Firefox */
  background: white;
}

::selection {
  background: white;
}


body{
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscalze;
	overflow-x: hidden;
	width: 100%;
	height: 100%;
	font-family: 'Barlow', sans-serif;
}

.bg-canvas{
	background: rgb(255,244,242);
	position: absolute;
	top: 0;
	left: 0;
	z-index: -1;
}

/*Header*/

header{
	padding: 20px;
}

.innerHeader{
	display: flex;
	align-items: center;
}


.title{
	font-size: clamp(30px, 10vw, 150px);
	flex-grow: 1;
	font-weight: bold;
}

.title span {
	overflow: hidden;
}

.date{
	writing-mode: vertical-rl;
	text-orientation: mixed;
	opacity: 0.5;
	font-size: 10px;
}

/*Header*/

section{
	margin: 30px 0 30px 0;
}

.content__paragraph{
	margin: 0 auto;
	width: 80%;
	text-align: center;
}

.paragraph{
	font-size: clamp(20px, 3vw, 90px);
	overflow: hidden;
}


#canvas3d{
	height: 90vw !important;
	width: 125vw !important;
	transform: translate(-12vw);
}

.gallerie{
	display: flex;
	flex-direction: column;
	align-items: center;
}

.gallerie__item{
	margin-bottom: 40px;
	position: relative;
	max-width: 900px;
}

.gallerie__item--img{	
	width: 100%;
}

.gallerie__item--caption{
	position: absolute;
	bottom: 0;
	left: 8%;
	font-size: clamp(12px, 2vw, 40px);
	z-index: -100;
	opacity: 0;
}


/*Section*/


/*Footer*/

footer{
	height: 60px;
	padding: 10px;
}

.signature{
	width: 100px;
	transform: rotate(-22deg);
}

.signature a {
	font-family: 'Square Peg', cursive;
	text-decoration: none;
	color: black;
	font-size: clamp(20px, 2vw, 50px);
	opacity: 0.6;
} 

.signature a:visited {
	color: black;
} 

/*Footer*/

